/* =============table========== */

table {
    border-collapse: collapse;
    width: 100%;
}

th, td {
    font-size: 1rem;
    padding: 10px;
    text-align: center;
}

td {
    border-bottom: 1px solid rgba(0, 0, 0, .1);
}

th {
